import React from 'react';
import { admin } from '../Admin/Admin';
import { AiOutlineDoubleRight } from 'react-icons/ai';
import './Hero.css';
import { HeroBox } from './HeroBox/HeroBox';

export const Hero = () => {
    return (
        <section className="hero">
            <h5 style={{color : `${admin.Style.text}`}}>{admin.Content.HeroW}</h5>
            <h1 className="heroTitle" style={{color : `${admin.Style.text}` , fontFamily : `${admin.Style.sFamily}`}}>{admin.Content.HeroT}</h1>

            <div className="flex-between addressBox" > 
                <input type="text" defaultValue={admin.Content.UserAddress } placeholder={admin.Content.UserAddressPlaceholder} />
                <button className="flex" style={{color : `${admin.Style.DarkText}`}}>Buy Kodi   <span><AiOutlineDoubleRight /></span></button>
            </div>

            <div className="holdings">
                <h2 style={{color : `${admin.Style.text}`}}>{admin.Content.HoldingT}</h2>
                <h1 style={{color : `${admin.Style.text}`}}>{admin.Content.TokenHoldings}</h1>
            </div>

            <div className="HeroBoxes">
                <HeroBox 
                    title = {admin.Content.TBPT}
                    value = {admin.Content.TBPValue}
                />
                <HeroBox 
                    title = {admin.Content.LPTT}
                    value = {admin.Content.LPTValue}
                />
                <HeroBox 
                    title = {admin.Content.NPLT}
                    value = {admin.Content.NPLValue}
                />
            </div>

            <button className="flex connect" style={{color : `${admin.Style.DarkText}`}}>{admin.Content.HeroOT}   <span><AiOutlineDoubleRight /></span></button>
        </section>
    )
}
