import React from 'react'
import { admin } from '../Admin/Admin'
import './Holders.css';

export const Holders = () => {
    return (
        <section className="holders flex-center">
            <h1 style={{color : `${admin.Style.text}` , fontFamily: `${admin.Style.sFamily}`}}>{admin.Content.TotalT}</h1>
            <h1 style={{color : `${admin.Style.text}`}}>{admin.Content.TotalBNB}</h1>
            <h1 style={{color : `${admin.Style.text}`}}>{admin.Content.TotalUSD}</h1>

            <p style={{color : `${admin.Style.text}`}}>{admin.Content.TotalP}</p>

            <div className="flex tradingVolume">
                <h2 style={{color : `${admin.Style.text}`}}>Trading volume = </h2>
                <div><span style={{color : `${admin.Style.text}`}}>$</span> <input type="number" min="0" defaultValue={admin.Content.TradingVolume} /></div>
            </div>
        </section>
    )
}
