import React from 'react';
import { admin } from '../../Admin/Admin';
import './HeroBox.css';

export const HeroBox = ({title , value}) => {
    return (
        <div className="HeroBox">
            <h1 style={{color: `${admin.Style.sText}`}}>{title}</h1>
            <h2 style={{color: `${admin.Style.text}`}}>{value}</h2>
        </div>
    )
}
